import { BigNumber, Contract } from 'ethers';
import { parseUnits } from 'ethers/lib/utils';
import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import ABIS from '../../protocol/deployments/abi';
import { useGetActiveChainId } from '../../state/chains/hooks';
import useCore from '../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default (contractName: string, i: number, j: number, dx: string, dxTokenDecimals: number) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const activeChainId = useGetActiveChainId();

  const fetchValue = useCallback(async () => {
    const contract = core.contracts(contractName, activeChainId);

    if (!Number(dx)) {
      setValue({
        isLoading: false,
        value: BigNumber.from(0),
      });
    } else {
      const dxBN = BigNumber.from(parseUnits(dx, dxTokenDecimals));
      const outputAmount = await contract.get_dy_underlying(i, j, dxBN);

      setValue({
        isLoading: false,
        value: outputAmount,
      });
    }
  }, [i, j, dx, core, dxTokenDecimals, contractName, activeChainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch output amount from curveswap router %: ${err}`),
      );
    }
  }, [core.isUnlocked, activeChainId, contractName, fetchValue]);

  return value;
};
