import { BigNumber } from '@ethersproject/bignumber';
import { useCallback, useEffect, useMemo, useState } from "react";
import { useWallet } from 'use-wallet';

import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import useCore from '../../useCore';
import { FarmingConfigurations } from "../../../protocol/config";

type State = {
  isLoading: boolean;
  value: boolean;
};

export default () => {
  const [value, setValue] = useState<State>({ isLoading: true, value: false});

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();
  const chainId = useGetActiveChainId();
  const strategies = core.getAllStrategies(chainId, account);
  const checkingPool: string[] = strategies[0]?.stakingPools || [];

  const farmingPools: FarmingConfigurations[] = useMemo(() => {
    if (checkingPool.length === 0) return []
    return core.getFarmingPools(chainId).filter((item) => {
      // @ts-ignore
      if (checkingPool.includes(item.name)) {
        return item;
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
    })}, [core, chainId]);

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: false });
      return;
    }

    let hasBalance = 0;

    for (const item of farmingPools) {
      const contract = core.contracts(item.contract, chainId);
      const balance: BigNumber = await contract.balanceOf(core.myAccount);
      if (balance.gt(0)) hasBalance ++;
    }
    setValue({ isLoading: false, value: hasBalance >= farmingPools.length});
  }, [account, farmingPools, core, chainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch any staked amount: ${err}`));
    }
  }, [core.isUnlocked, blockNumber, account, fetchValue]);

  return value;
};
