import React from 'react';

import Grid from '@material-ui/core/Grid';

import PageHeader from "../../theme/components/PageHeader";
import PoolCard from "./components/PoolCard";
import useCore from "../../hooks/useCore";
import { useGetActiveChainId } from "../../state/chains/hooks";

const Pools = () => {
  const core = useCore();
  const chainId = useGetActiveChainId();
  const pools = core.getLiquidityPools(chainId);

  return (
      <div className="custom-container">
        <PageHeader
          title={'POOLS'}
          subTitle={'Borrow ARTH against different collaterals at 0% interest.'}
          showLearnMore={true}
          learnMoreLink={'https://docs.arthcoin.com/protocol/introduction-arth-loans'}
        />
        <Grid container className="m-t-24" spacing={2}>
          {pools.map((poolInfo) => {
            return (
              <Grid item lg={4} md={4} sm={12} xs={12}>
                <PoolCard poolStaticInfo={poolInfo}/>
              </Grid>
            )})
          }
        </Grid>
      </div>
  );
};

export default Pools;
