import React, { useEffect, useMemo, useState } from "react";
import useTokenBalance from "../../../hooks/state/useTokenBalance";
import { BigNumber } from "ethers";
import { parseUnits } from "ethers/lib/utils";
import { BNZERO, DECIMALS_18 } from "../../../protocol/constants";
import useApprove, { ApprovalState } from "../../../hooks/callbacks/useApprove";
import TextWrapper from "../../../theme/components/TextWrapper";
import theme from "../../../theme";
import { formatToBN, getDisplayBalance } from "../../../utils/formatBalance";
import Grid from "@material-ui/core/Grid";
import SlippageContainer from "../../../components/SlippageContainer";
import InputContainer from "../../../theme/components/InputContainer";
import Input from "../../../theme/components/Input";
import CollateralDropDown from "../../../theme/components/CollateralDropDown";
import Button from "../../../theme/components/Button";
import { PoolsStaticInfo } from "../../../protocol/config";
import useCore from "../../../hooks/useCore";
import { useGetActiveChainId } from "../../../state/chains/hooks";
import Inputs from "./Inputs";
import Price from "./Price";
import ApproveButtons from "../../AddStrategies/components/ApproveButtons";
import ShareOfPool from "./ShareOfPool";
import ConfirmButton from "./ConfirmButton";

interface IProps {
  poolStaticInfo: PoolsStaticInfo;
}

interface IApproval { [name: string]: 'Approved' | 'IsApproving' | 'NotApproved' }

const AddLquiditySection = (props: IProps) => {
  const {poolStaticInfo} = props;
  const core = useCore();
  const chainId = useGetActiveChainId();

  const [firstCoinAmount, setFirstCoinAmount] = useState<string>('');
  const [openDropDown, setOpenDropDown] = useState<boolean>(false);
  const [selectedToken, setSelectedToken] = useState<string>('ARTH');
  const supportedCollateral = ['ARTH', 'ARTH.usd'];

  const [underlyingtokensObj, setUnderlyingtokensObj] = useState<{ [name: string]: string }>({});
  const [underlyingtokensBalObj, setUnderlyingtokensBalObj] = useState<{ [name: string]: BigNumber }>({});
  const [approvalStatus, setApprovalStatus] = useState<IApproval>({});

  useEffect(() => {
    poolStaticInfo.token3PoolUnderlyingTokens.forEach((t) => {
      const supply = underlyingtokensObj;
      supply[t] = '';
      const bal = underlyingtokensBalObj;
      bal[t] = BNZERO;
      const status = approvalStatus;
      status[t] = 'NotApproved';

      setUnderlyingtokensObj(supply);
      setUnderlyingtokensBalObj(bal)
      setApprovalStatus({ ...status });

    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [poolStaticInfo.token3PoolUnderlyingTokens]);

  const [confirmModal, setConfirmModal] = useState<boolean>(false);

  const firstCoinERC20 = core.tokens(selectedToken, chainId);

  const firstCoinTokenBalance = useTokenBalance(firstCoinERC20);

  const isFirstCoinInputGreaterThanBalance = useMemo(() => {
    return firstCoinTokenBalance.value.lt(BigNumber.from(parseUnits(firstCoinAmount || '0', firstCoinERC20.decimal)))
  }, [firstCoinAmount, firstCoinTokenBalance, firstCoinERC20]);

  const [firstCoinApproveStatus, approveFirstCoin] = useApprove(
    firstCoinERC20,
    core.contracts('SwapRouter', chainId).address,
  );

  const isFirstCoinApproved = useMemo(() => firstCoinApproveStatus === ApprovalState.APPROVED, [firstCoinApproveStatus]);
  const isFirstCoinApproving = useMemo(() => firstCoinApproveStatus === ApprovalState.PENDING, [firstCoinApproveStatus]);

  const isButtonDisabled = useMemo(() => {
    let checks = 0;

    for (const [key, value] of Object.entries(underlyingtokensObj)) {
      const token = core.tokens(key, chainId);
      if (value !== '') checks++;
      if (underlyingtokensBalObj[key].gte(formatToBN(underlyingtokensObj[key], token.decimal))) {
        checks++;
      }
      if (approvalStatus[key] === 'Approved') checks ++
    }

    if (!isFirstCoinInputGreaterThanBalance) checks ++;
    if (firstCoinAmount !== '') checks ++;
    if (isFirstCoinApproved) checks ++;

    return checks !== ((poolStaticInfo.token3PoolUnderlyingTokens.length * 3) + 3)
  }, [approvalStatus,
    chainId,
    core,
    firstCoinAmount,
    isFirstCoinApproved,
    isFirstCoinInputGreaterThanBalance,
    poolStaticInfo.token3PoolUnderlyingTokens.length, underlyingtokensBalObj, underlyingtokensObj]);

  const approvalButtons = useMemo(() => poolStaticInfo.token3PoolUnderlyingTokens.map((token) => {
    const onlyOneCollateral = poolStaticInfo.token3PoolUnderlyingTokens.length === 1
    return (
      <Grid item lg={onlyOneCollateral ? 12 : 6} md={onlyOneCollateral ? 12 : 6} sm={12} xs={12}>
        <ApproveButtons
          key={token}
          token={token}
          contract={'SwapRouter'}
          updateStatus={(data) => {
            if (approvalStatus[token] !== data) {
              const temp = approvalStatus;
              temp[token] = data;
              setApprovalStatus({ ...temp });
            }
          }}
        />
      </Grid>
    )
  }), [approvalStatus, poolStaticInfo.token3PoolUnderlyingTokens]);

  return (
    <div>

      {/*<ConfirmLiquidity
        lpToken={poolStaticInfo.lpToken}
        selectedToken={selectedToken}
        selectedTokenAmount={firstCoinAmount}
        firstToken={poolStaticInfo.token3PoolUnderlyingTokens[0]}
        secondToken={poolStaticInfo.token3PoolUnderlyingTokens[1]}
        thirdToken={poolStaticInfo.token3PoolUnderlyingTokens[2]}
        firstTokenDecimal={secondCoinERC20.decimal}
        secondTokenDecimal={thridCoinERC20.decimal}
        thirdTokenDecimal={forthCoinERC20.decimal}
        firstTokenAmount={secondCoinAmount}
        secondTokenAmount={thirdCoinAmount}
        thirdTokenAmount={fourthCoinAmount}
        shareOfPool={Number(getDisplayBalance(yourShare, 18, 6)).toLocaleString('en-US', { maximumFractionDigits: 6}) + '%'}
        lpTokenOut={Number(getDisplayBalance(lpTokensOut.value, 18, 6)).toString()}
        open={confirmModal}
        onClose={() => setConfirmModal(false)}
      />*/}
      <Grid container>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <div className={'material-secondary'} style={{position: 'relative'}}>
            <SlippageContainer />
            <div className={'m-b-16'}/>
            <InputContainer
              label={'Enter Amount'}
              dataLabel={'Balance:'}
              dataValue={Number(getDisplayBalance(firstCoinTokenBalance.value, 18, 18)).toLocaleString()}
              className={'m-b-16'}
            >
              <div className={'single-line-center-between'}>
                <Input
                  value={firstCoinAmount}
                  setValue={setFirstCoinAmount}
                  maxTag={true}
                  onMaxClick={() => {
                    setFirstCoinAmount(Number(getDisplayBalance(firstCoinTokenBalance.value, firstCoinERC20.decimal, 6)).toString());
                  }}
                />
                <CollateralDropDown
                  selectedCollateral={selectedToken}
                  collateralTypes={supportedCollateral}
                  showDropDown={openDropDown}
                  toggleDropDown={() => setOpenDropDown(!openDropDown)}
                  ondropDownValueChange={(data) => {
                    setOpenDropDown(false)
                    setSelectedToken(data)
                  }}
                  hasDropDown={true}
                />
              </div>
              {
                isFirstCoinInputGreaterThanBalance && <TextWrapper
                  text={`Input cannot be greater than your ${selectedToken} balance`}
                  Fcolor={theme.color.red[300]}
                  fontSize={12}
                  className={'m-t-4'}
                />
              }
            </InputContainer>

            {
              poolStaticInfo.token3PoolUnderlyingTokens.map((item) => {
                return (
                  <Inputs
                    selectedToken={item}
                    setTokenSupplyObj={(key, value) => {
                      const temp = underlyingtokensObj;
                      temp[key] = value;
                      setUnderlyingtokensObj({ ...temp });
                    }}
                    setTokenBalanceObj={(key, value) => {
                      if (!underlyingtokensBalObj[key].eq(value)) {
                        const temp = underlyingtokensBalObj;
                        temp[key] = value;
                        setUnderlyingtokensBalObj({ ...temp });
                      }
                    }}
                  />
                )
              })
            }
            <div className={'m-t-12'}>
              <Price minterContract={poolStaticInfo.minterContract} />
              <ShareOfPool
                lpToken={poolStaticInfo.lpToken}
                minterContract={poolStaticInfo.minterContract}
                selectedToken={selectedToken}
                firstCoinAmount={firstCoinAmount}
                underlyingtokensObj={underlyingtokensObj}
                token3PoolUnderlyingTokens={poolStaticInfo.token3PoolUnderlyingTokens}
              />
            </div>
            <div className={'m-t-32'}>
              <Grid container spacing={2} direction={'row'}>
                <Grid item lg={6} md={6} sm={12} xs={12}>
                  <Button
                    text={isFirstCoinApproved
                        ? `${selectedToken} Approved`
                        : `Approve ${selectedToken}`
                    }
                    onClick={
                      !isFirstCoinApproved
                        ? approveFirstCoin
                        : () => {}
                    }
                    disabled={isFirstCoinApproved}
                    loading={isFirstCoinApproving}
                    tracking_id={'add_liquidity_approval'}
                    tracking_params={{
                      collateral: selectedToken,
                    }}
                  />
                </Grid>
                {approvalButtons}
                <Grid item lg={12} md={12} sm={12} xs={12}>
                  {Object.keys(underlyingtokensObj).length > 0 &&<ConfirmButton
                    isButtonDisabled={isButtonDisabled}
                    selectedToken={selectedToken}
                    token3PoolUnderlyingTokens={poolStaticInfo.token3PoolUnderlyingTokens}
                    firstCoinAmount={firstCoinAmount}
                    minterContract={poolStaticInfo.minterContract}
                    underlyingtokensObj={underlyingtokensObj}
                  />}
                </Grid>
              </Grid>
            </div>
          </div>
        </Grid>
      </Grid>
    </div>
  )
}

export default AddLquiditySection;
