import { BigNumber } from 'ethers';
import { useCallback } from 'react';
import { useWallet } from 'use-wallet';

import { useAddPopup } from '../../state/application/hooks';
import { useGetActiveChainId } from '../../state/chains/hooks';
import { useTransactionAdder } from '../../state/transactions/hooks';
import { getDisplayBalance } from '../../utils/formatBalance';
import formatErrorMessage from '../../utils/formatErrorMessage';
import useCore from '../useCore';
import { useSlippage } from '../../state/slippage/hooks';

export default function (
  symbol: string,
  arthUsdIn: BigNumber,
  busdIn: BigNumber,
  usdcIn: BigNumber,
  usdtIn: BigNumber,
  minLpTokenOut: BigNumber
) {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const chainId = useGetActiveChainId();
  const slippage = useSlippage();

  const action = useCallback(
    async (callback?: () => void): Promise<void> => {
      const contract = core.contracts('SwapRouter', chainId);

      const slippageRounded = Math.floor(slippage.value * 1e3) / 1e3;
      const slippageAdjustedAmountout = !Number(slippage.value)
        ? minLpTokenOut.mul(99).div(100)
        : minLpTokenOut.sub(minLpTokenOut.mul(slippageRounded * 1e3).div(1e5))
      
      try {
        const response = await contract.addLiquidityUsingARTHusd(
          arthUsdIn,
          busdIn,
          usdcIn,
          usdtIn,
          slippageAdjustedAmountout.mul(99).div(100),
          core.myAccount,
          Math.floor(Date.now() / 1000) + 5 * 60
        );

        addTransaction(response, {
          summary: `Add Liquidity for ${
            Number(getDisplayBalance(minLpTokenOut,18)).toLocaleString('en-US', { maximumFractionDigits: 5})
          } ${symbol.toUpperCase()}`,
        });

        if (callback) callback();
      } catch (e) {
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
      }
    },
    [
      core, 
      chainId, 
      addTransaction, 
      slippage.value, 
      addPopup,
      arthUsdIn,
      busdIn,
      usdcIn,
      usdtIn,
      minLpTokenOut, 
      symbol
    ],
  );

  return action;
}
