import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useWallet } from 'use-wallet';

import { DECIMALS_18 } from '../../protocol/constants';
import { AppState } from '../../state';
import { useGetActiveChainId } from '../../state/chains/hooks';
import useCore from '../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default () => {
  const [value, setValue] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const activeChainId = useGetActiveChainId();
  // const gmuPrices = useSelector((app: AppState) => app.analytics.gmuPrice);
  const entireSystemColl = useSelector((app: AppState) => app.analytics.entireSystemColl);
  const { account } = useWallet();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    const collaterals = core.getAllTroves(activeChainId);

    // const collaterals = core.getCollateralTypes(activeChainId);

    // Fetch respective debt.
    const debts = collaterals.map((c: string) => entireSystemColl[c]);

    // Fetch respective collateral price.
    // const prices = collaterals.map((c: string) => gmuPrices[c] || BigNumber.from(0));

    const prices: BigNumber[] = [];
    for (const collateral of collaterals) {
      const contract = core.getPriceFeed(collateral, activeChainId);
      prices.push(await contract.callStatic.fetchPrice());
    }
    if (debts.findIndex((f) => f === undefined) >= 0)
      return setValue({ isLoading: true, value: BigNumber.from(0) });

    const gmuDebts = debts.map((debt, i) => debt.mul(prices[i]).div(DECIMALS_18));

    setValue({
      isLoading: false,
      value: gmuDebts.reduce((a, b) => a.add(b), BigNumber.from(0)),
    });
  }, [account, core, entireSystemColl, activeChainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch protocol collateral in gmu %: ${err}`),
      );
    }
  }, [core.isUnlocked, account, fetchValue, activeChainId]);

  return value;
};
