import clsx from 'clsx';
import React, { useState } from 'react';
import Loader from 'react-spinners/BeatLoader';

import { makeStyles } from '@material-ui/core/styles';
import { DataGrid } from '@material-ui/data-grid';

import '../../../customCss/Custom-Mahadao-Data-Table.css';
import useGetAllOpenTroves from '../../../hooks/state/useGetAllOpenTroves';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import IconLoader from '../../../theme/components/IconLoader';
import TextWrapper from '../../../theme/components/TextWrapper';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';
import { useWallet } from "use-wallet";

export function SortedDescendingIcon() {
  return <IconLoader iconName={'ArrowUp'} iconType="arrow" />;
}

export function SortedAscendingIcon() {
  return <IconLoader iconName={'ArrowDown'} iconType="arrow" />;
}

const useStyles = makeStyles({
  root: {
    '& .super-app-theme--header': {
      cursor: 'pointer',
    },
    '& .super-app.negative': {
      color: '#FA4C69',
      fontWeight: '600',
    },
    '& .super-app.positive': {
      color: '#20C974',
      fontWeight: '600',
    },
    '& .super-app.loss': {
      color: '#FA4C69',
      fontWeight: '600',
    },
    '& .super-app.profit': {
      color: '#20C974',
      fontWeight: '600',
    },
  },
  gridStyle: {
    color: 'white',
  },
});

type Props = {
  doRefresh: boolean;
};

const YourLoansTable = ({ doRefresh }: Props) => {
  const [rows] = useState<number>(10);

  const classes = useStyles();
  const { account } = useWallet();
  const troves = useGetAllOpenTroves(doRefresh);
  const chainId = useGetActiveChainId();

  const columns = [
    {
      field: 'token',
      headerName: 'TOKEN',
      flex: 0.167,
      sortable: false,
      renderCell: (params: any) => {
        return (
          <div className="single-line-center-start">
            <IconLoader iconName={params.value} iconType="tokenSymbol" height={44} width={44} />
            <span className="m-l-12">{params.value}</span>
          </div>
        );
      },
    },
    {
      field: 'avail_to_mint',
      headerName: 'AVAIL TO MINT',
      flex: 0.167,
      sortable: false,
      renderCell: (params: any) => {
        return `${Number(getDisplayBalance(params.value, 18, 3)).toLocaleString()} ARTH`;
      },
      renderHeader: (params: any) => {
        return (
          <div className="single-line-center-start">
            <IconLoader iconName={'ARTH'} iconType="tokenSymbol" width={20} className="m-r-4" />
            {'AVAIL TO MINT'}
          </div>
        );
      },
    },
    {
      field: 'deposit',
      headerName: 'Deposit ',
      flex: 0.167,
      sortable: false,
      renderCell: (params: any) => {
        const depositToken = troveDetails(params.row.token, chainId).depositToken;
        return `${Number(
          getDisplayBalance(params.value, 18, 3),
        ).toLocaleString()} ${depositToken}`;
      },
    },
    {
      field: 'debt',
      headerName: 'DEBT (ARTH)',
      flex: 0.167,
      sortable: false,
      renderCell: (params: any) => {
        return `${Number(getDisplayBalance(params.value, 18, 3)).toLocaleString()}`;
      },
      renderHeader: (params: any) => {
        return (
          <div className="single-line-center-start">
            <IconLoader iconName={'ARTH'} iconType="tokenSymbol" width={20} className="m-r-4" />
            {params.field}
          </div>
        );
      },
    },
    {
      field: 'avail_to_withdraw',
      headerName: 'AVAIL To WITHDRAW',
      flex: 0.167,
      sortable: false,
      renderCell: (params: any) => {
        const depositToken = troveDetails(params.row.token, chainId).depositToken;
        return `${Number(
          getDisplayBalance(params.value, 18, 3),
        ).toLocaleString()} ${depositToken}`;
      },
    },
    {
      field: 'collateralRatio',
      headerName: 'COLLATERAL RATIO',
      flex: 0.167,
      sortable: false,
      cellClassName: (params: any) =>
        clsx('super-app', {
          negative: params.row.isLiquidateable,
          positive: !params.row.isLiquidateable,
        }),
      renderCell: (params: any) => {
        return `${Number(params.value).toLocaleString()}%`;
      },
    },
  ];

  const noResultsComponent = () => {
    return (
      <div className="noResultContainer">
        <div className="internal">
          <TextWrapper
            text={'You havent opened any loan yet'}
            fontSize={24}
            fontWeight={'bold'}
            className="m-b-8"
            align="center"
          />
          <TextWrapper
            text={'Open Interest free loan with leveraged position.'}
            fontSize={18}
            className="m-b-8"
            Fcolor={'#FFFFFF64'}
            align={'center'}
          />
          <div style={{ width: '130px', margin: 'auto' }} className="m-t-24">
            <Button
              to={'/loan/add'}
              size={'sm'}
              tracking_id={'open_loan'}
              tracking_params={{ position: 'your_loans_table' }}
              variant={'transparent'}
            >
              <IconLoader iconName={'Add'} className="m-r-4" width={16} />
              Open Loan
            </Button>
          </div>
        </div>
      </div>
    );
  };

  const LoadingOverlayComponent = () => {
    return (
      <div className="noResultContainer">
        <div className="internal">
          <Loader color={'#ffffff'} loading={true} size={10} margin={2} />
        </div>
      </div>
    );
  };

  return (
    <React.Fragment>
      <div className={classes.root} style={{ position: 'relative' }}>
        <DataGrid
          className={classes.gridStyle}
          getRowId={(rows) => {
            return rows.id;
          }}
          rows={troves.value}
          columns={columns}
          pageSize={rows}
          hideFooterPagination={true}
          disableMultipleSelection={true}
          rowHeight={84}
          autoHeight={true}
          disableColumnMenu={true}
          onRowClick={(newSelection) => {
            window.open(`/#/loan/details/${account}/${newSelection.row.token}`, '_self');
          }}
          loading={troves.isLoading}
          components={{
            LoadingOverlay: LoadingOverlayComponent,
            NoRowsOverlay: noResultsComponent,
            ColumnSortedDescendingIcon: SortedDescendingIcon,
            ColumnSortedAscendingIcon: SortedAscendingIcon,
          }}
          disableColumnSelector={true}
          disableSelectionOnClick={true}
        />
      </div>
    </React.Fragment>
  );
};

export default YourLoansTable;
