import React from "react";
import DataField from "../../../theme/components/DataField";
import { getDisplayBalance } from "../../../utils/formatBalance";
import useGet3PoolVirtualPrice from "../../../hooks/state/pool/useGet3PoolVirtualPrice";

interface IProps {
  minterContract: string;
}

const Price = (props: IProps) => {
  const {minterContract} = props;
  const price = useGet3PoolVirtualPrice(minterContract);

  return (
    <DataField
      label={'Price'}
      value={Number(getDisplayBalance(price.value, 18, 6)).toLocaleString()}
      valueTag={'USD'}
      className={'m-b-8'}
    />
  )
}

export default Price
