import Button from "../../../theme/components/Button";
import React from "react";
import useAddARTHusdLiquidity from "../../../hooks/callbacks/useAddARTHusdLiquidity";
import { BigNumber } from "ethers";
import { parseUnits } from "ethers/lib/utils";
import useAddARTHLiquidity from "../../../hooks/callbacks/useAddARTHLiquidity";
import { getDisplayBalance } from "../../../utils/formatBalance";
import useGetLpTokenOut from "../../../hooks/state/pool/useGetLpTokenOut";
import useCore from "../../../hooks/useCore";
import { useGetActiveChainId } from "../../../state/chains/hooks";

interface IProps {
  isButtonDisabled: boolean;
  selectedToken: string;
  token3PoolUnderlyingTokens: string[];
  firstCoinAmount: string;
  minterContract: string;
  underlyingtokensObj: object;
}

const ConfirmButton = (props: IProps) => {
  const {isButtonDisabled, selectedToken, token3PoolUnderlyingTokens, firstCoinAmount, minterContract, underlyingtokensObj} = props;

  const core = useCore();
  const chainId = useGetActiveChainId();
  const firstCoinERC20 = core.tokens(selectedToken, chainId);
  const secondCoinERC20 = core.tokens(token3PoolUnderlyingTokens[0], chainId);
  const thridCoinERC20 = core.tokens(token3PoolUnderlyingTokens[1], chainId);
  const forthCoinERC20 = core.tokens(token3PoolUnderlyingTokens[2], chainId);

  // @ts-ignore
  const secondCoinAmount = underlyingtokensObj['BUSD'] || underlyingtokensObj['DAI'];
  // @ts-ignore
  const thirdCoinAmount = underlyingtokensObj['USDC'];
  // @ts-ignore
  const fourthCoinAmount = underlyingtokensObj['USDT'];

  const lpTokensOut = useGetLpTokenOut(
    minterContract,
    selectedToken === 'ARTH.usd'
      ? BigNumber.from(parseUnits(firstCoinAmount || '0', firstCoinERC20.decimal))
      : BigNumber.from(parseUnits(firstCoinAmount || '0', firstCoinERC20.decimal)).mul(2e6).div(1e6),
    BigNumber.from(parseUnits(secondCoinAmount || '0', secondCoinERC20.decimal)),
    BigNumber.from(parseUnits(thirdCoinAmount || '0', thridCoinERC20.decimal)),
    BigNumber.from(parseUnits(fourthCoinAmount || '0', forthCoinERC20.decimal))
  );

  const arthUsdHandler = useAddARTHusdLiquidity(
    Number(getDisplayBalance(lpTokensOut.value, 18, 6)).toString(),
    BigNumber.from(parseUnits(firstCoinAmount || '0', firstCoinERC20.decimal)),
    BigNumber.from(parseUnits(secondCoinAmount || '0', secondCoinERC20.decimal)),
    BigNumber.from(parseUnits(thirdCoinAmount || '0', thridCoinERC20.decimal)),
    BigNumber.from(parseUnits(fourthCoinAmount || '0', forthCoinERC20.decimal)),
    BigNumber.from(parseUnits(Number(getDisplayBalance(lpTokensOut.value, 18, 6)).toString() || '0', 18))
  );

  const arthHandler = useAddARTHLiquidity(
    Number(getDisplayBalance(lpTokensOut.value, 18, 6)).toString(),
    BigNumber.from(parseUnits(firstCoinAmount || '0', firstCoinERC20.decimal)),
    BigNumber.from(parseUnits(secondCoinAmount || '0', secondCoinERC20.decimal)),
    BigNumber.from(parseUnits(thirdCoinAmount || '0', thridCoinERC20.decimal)),
    BigNumber.from(parseUnits(fourthCoinAmount || '0', forthCoinERC20.decimal)),
    BigNumber.from(parseUnits(Number(getDisplayBalance(lpTokensOut.value, 18, 6)).toString() || '0', 18))
  );

  return (
    <Button
      disabled={isButtonDisabled}
      text={'Supply'}
      size={'lg'}
      onClick={selectedToken === 'ARTH.usd' ? arthUsdHandler : arthHandler}
    />
  )
}

export default ConfirmButton;
