import React, { useMemo } from 'react';
import styled from 'styled-components';

import useTokenBalance from '../../../hooks/state/useTokenBalance';
import useCore from '../../../hooks/useCore';
import { PoolsStaticInfo, TokeniseStaticInfo } from "../../../protocol/config";
import { useGetActiveChainId } from '../../../state/chains/hooks';
import theme from '../../../theme';
import Button from '../../../theme/components/Button';
import IconLoader from '../../../theme/components/IconLoader';
import TextButton from '../../../theme/components/TextButton';
import TextWrapper from '../../../theme/components/TextWrapper';
import ActionButton from "../../../theme/components/ActionButton";
import DataField from "../../../theme/components/DataField";
import { getDisplayBalance } from '../../../utils/formatBalance';
import useTotalSupply from '../../../hooks/useTotalSupply';
import { DECIMALS_18 } from '../../../protocol/constants';
import { BigNumber } from 'ethers';
import useTokenBalanceOf from '../../../hooks/state/useTokenBalanceOf';

interface IProps {
  poolStaticInfo: PoolsStaticInfo;
}

const PoolCard = (props: IProps) => {
  const {poolStaticInfo} = props;
  const core = useCore();
  const chainId = useGetActiveChainId();
  const lpERC20 = core.tokens(poolStaticInfo.lpToken, chainId);
  const lpERC20Balance = useTokenBalance(lpERC20);
  const lpERC20TotalSupply = useTotalSupply(poolStaticInfo.lpToken);

  const yourShare = useMemo(() => {
    if (lpERC20TotalSupply.value.lte(0)) return BigNumber.from(0);
    return lpERC20Balance.value.mul(DECIMALS_18).mul(100).div(lpERC20TotalSupply.value)
  }, [lpERC20Balance, lpERC20TotalSupply]);

  const arthUsdInPool = useTokenBalanceOf(
    core.tokens('ARTH.usd', chainId),
    core.contracts(poolStaticInfo.minterContract, chainId).address
  );
  const token3PoolInPool = useTokenBalanceOf(
    core.tokens(poolStaticInfo.token3Pool, chainId),
    core.contracts(poolStaticInfo.minterContract, chainId).address
  );

  return (
    <Card className={'material-secondary'}>
      <div className={'m-b-24'}>
        <div className={'single-line-center-center m-b-12'}>
          <ImgCon>
            <IconLoader
              iconName={poolStaticInfo.symbol}
              iconType={'tokenSymbol'}
              height={98}
              width={192}
            />
          </ImgCon>
        </div>
        <TextWrapper
          text={poolStaticInfo.name}
          fontSize={24}
          fontFamily={'Syne'}
          align={'center'}
          fontWeight={600}
        />
      </div>
      <div className={'single-line-center-center m-b-24'}>
        <TextWrapper
          text={poolStaticInfo.desc}
          Fcolor={theme.color.transparent[100]}
        />
      </div>
      <div className={'m-b-32'}>
        <DataField
          label={'Your total pool tokens'}
          value={Number(getDisplayBalance(lpERC20Balance.value, lpERC20.decimal, 6)).toLocaleString()}
          valueTag={poolStaticInfo.lpToken}
          className={'m-b-12'}
        />
        <DataField
          label={'Pooled ARTH'}
          value={Number(getDisplayBalance(
            arthUsdInPool.value,
            18
          )).toLocaleString()}
          valueTag={'ARTH'.toUpperCase()}
          className={'m-b-12'}
        />
        <DataField
          label={`Pooled ${poolStaticInfo.token3Pool}`}
          value={Number(getDisplayBalance(
            token3PoolInPool.value,
            core.tokens(poolStaticInfo.token3Pool, chainId).decimal
          )).toLocaleString()}
          valueTag={poolStaticInfo.token3Pool}
          className={'m-b-12'}
        />
        <DataField
          label={'Your pool share'}
          value={Number(getDisplayBalance(yourShare, 18, 6)).toLocaleString() + '%'}
          className={'m-b-12'}
        />
      </div>
      <ButtonPosition>
        <div>
          <ActionButton
            text={'Add Liquidity'}
            onClick={() => window.location.href = `/#/pools/add-liquidity/${poolStaticInfo.name}`}
            tracking_id={'add_liquidity'}
            tracking_params={{
              collateral: '',
              action: 'initial',
            }}
          />
        </div>
        <div className={'single-line-center-center m-t-24'}>
          <TextButton
            onClick={() => window.open(poolStaticInfo.removeLiquidityLink)}
            text={'Remove Liquidity'}
            tracking_id={'tokenize_redeem'}
            tracking_params={{
              collateral: '',
              action: 'initial',
            }}
          />
        </div>
      </ButtonPosition>
    </Card>
  );
};

export default PoolCard;

const Card = styled.div`
  padding: 32px;
  border-radius: 12px;
  height: 100%;
  @media(max-width: 600px) {
    padding: 32px 24px;
  }
`;

const ImgCon = styled.div`
  //width: 98px;
  //height: 98px;
  //box-shadow: 2px 2px 20px #2A2827;
  //border-radius: 50%;
  position: relative;
`;

const PlatformIcon = styled.div`
  position: absolute;
  top: -4px;
  right: -4px;
`;

const ButtonPosition = styled.div``;
