import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';

import { useBlockNumber } from '../state/application/hooks';
import { useGetActiveChainId } from '../state/chains/hooks';

import useCore from './useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default (tokenSymbol: string) => {
  const [totalSupply, setTotalSupply] = useState<State>({
    isLoading: true,
    value: BigNumber.from(0),
  });

  const core = useCore();
  const blockNumber = useBlockNumber();
  const activeChainId = useGetActiveChainId();

  const fetchValue = useCallback(async () => {
    const value = await core.tokens(tokenSymbol, activeChainId).totalSupply();
    if (!value.eq(totalSupply.value)) {
      setTotalSupply({
        isLoading: false,
        value: await core.tokens(tokenSymbol, activeChainId).totalSupply()
      });
    }
  }, [activeChainId, core, tokenSymbol, totalSupply.value]);

  useEffect(() => {
    fetchValue().catch((err) =>
      console.error(`Failed to fetch ${tokenSymbol} supply: ${err.stack}`),
    );
  }, [blockNumber, totalSupply, core, tokenSymbol, fetchValue]);

  return totalSupply;
};
