import { BigNumber } from 'ethers';
import { parseUnits } from 'ethers/lib/utils';
import React, { useMemo, useState } from 'react';
import Loader from 'react-spinners/BeatLoader';

import Grid from '@material-ui/core/Grid';

import CustomInputContainer from '../../../components/CustomInputContainer';
import { ValidateNumber } from '../../../components/CustomInputContainer/RegexValidation';
import useStake from '../../../hooks/callbacks/stabilityPool/useStake';
import useApprove, { ApprovalState } from '../../../hooks/callbacks/useApprove';
import useTokenBalance from '../../../hooks/state/useTokenBalance';
import useCore from '../../../hooks/useCore';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import theme from '../../../theme';
import Button from '../../../theme/components/Button';
import Modal from '../../../theme/components/Modal';
import TextWrapper from '../../../theme/components/TextWrapper';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';

interface Iprops {
  openModal: boolean;
  onClose: () => void;
  trove: string;
  unClaimedReward: { isLoading: boolean; value: BigNumber };
}

const StakeArthModal = (props: Iprops) => {
  const { openModal, onClose, trove, unClaimedReward } = props;

  const [arthValue, setARTHValue] = useState<string>('0');
  const [isInputFieldError, setIsInputFieldError] = useState<boolean>(false);

  const core = useCore();
  const chainId = useGetActiveChainId();
  const arthBalance = useTokenBalance(core.tokens('ARTH', chainId));
  const troveDetail = troveDetails(trove, chainId);

  const [arthApprovalStatus, approveARTH] = useApprove(
    core.tokens('ARTH', chainId),
    core.getController(troveDetail.contractPrefix, chainId)?.address || undefined,
  );

  const stake = useStake(troveDetail.contractPrefix, BigNumber.from(parseUnits(arthValue, 18)));

  const handleClose = () => {
    setARTHValue('0');
    onClose();
  };

  const handleStake = () => {
    stake(() => {
      setARTHValue('0');
      handleClose();
    });
  };

  const isARTHApproved = useMemo(() => arthApprovalStatus === ApprovalState.APPROVED, [
    arthApprovalStatus,
  ]);
  const isARTHApproving = useMemo(() => arthApprovalStatus === ApprovalState.PENDING, [
    arthApprovalStatus,
  ]);

  const onValueChange = async (val: string) => {
    if (val === '') {
      setARTHValue('0');
      return;
    }

    const check: boolean = ValidateNumber(val);
    if (!check) return;
    setARTHValue(check ? val : String(Number(val)));
  };

  return (
    <>
      <Modal
        title={'Deposit ARTH'}
        open={openModal}
        handleClose={() => handleClose()}
        closeButton
      >
        <CustomInputContainer
          isBalanceLoading={arthBalance.isLoading}
          IBalanceValue={getDisplayBalance(BigNumber.from(arthBalance.value), 18)}
          ILabelValue={'How much ARTH would you like to Deposit?'}
          showBalance={false}
          DefaultValue={arthValue}
          LogoSymbol={'ARTH'}
          SymbolText={'ARTH'}
          dontShowBackgroundContainer={true}
          setText={(t) => onValueChange(t)}
          inputMode={'decimal'}
          tagText={'MAX'}
          hasDropDown={false}
          errorCallback={(flag: boolean) => {
            setIsInputFieldError(flag);
          }}
        />
        <div className="single-line-center-between m-t-4 m-b-32">
          <div />
          <div className="single-line-center-between">
            <TextWrapper text={'Balance'} Fcolor={'#FFFFFF88'} />
            <div className="tags m-l-4">
              {arthBalance.isLoading ? (
                <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
              ) : (
                <TextWrapper
                  text={`${Number(
                    getDisplayBalance(arthBalance.value, 18, 3),
                  ).toLocaleString()} ARTH`}
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              )}
              <TextWrapper text={''} fontSize={12} Fcolor={'#FFFFFF88'} />
            </div>
          </div>
        </div>
        {unClaimedReward.value.gt(0) && (
          <div className="single-line-center-between m-t-12 m-b-12">
            <div className="single-line-center-start">
              <TextWrapper
                // toolTipText={`This amount is the ${collateralPool.toUpperCase()} earned from ${collateralPool.toUpperCase()} liquidation bonuses.`}
                text={'Gain you will recieve'}
                fontSize={12}
              />
            </div>
            <div className="single-line-center-start">
              {unClaimedReward.isLoading ? (
                <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
              ) : (
                <TextWrapper
                  text={
                    Number(getDisplayBalance(unClaimedReward.value, 18, 3)).toLocaleString() +
                    ' ' +
                    troveDetail.depositToken.toUpperCase()
                  }
                  fontSize={12}
                  Fcolor={theme.color.green[300]}
                />
              )}
            </div>
          </div>
        )}
        <Grid container spacing={2}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              variant={'transparent'}
              onClick={() => handleClose()}
              tracking_id={unClaimedReward.value.gt(0) ? `deposit&claim_S_P` : `deposit_S_P`}
              tracking_params={{
                collateral: troveDetail.trove,
                action: 'cancel',
                amount: Number(arthValue),
              }}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>

            <Button
              disabled={isInputFieldError || !Number(arthValue)}
              onClick={handleStake}
              tracking_id={unClaimedReward.value.gt(0) ? `deposit&claim_S_P` : `deposit_S_P`}
              tracking_params={{
                collateral: troveDetail.trove,
                action: 'confirm',
                amount: Number(arthValue),
              }}
            >
              {unClaimedReward.value.gt(0) ? 'Deposit and Claim' : 'Deposit'}
            </Button>

          </Grid>
        </Grid>
      </Modal>
    </>
  );
};

export default StakeArthModal;
