import React, { useMemo, useState } from 'react';

import useCore from "../../hooks/useCore";
import { useGetActiveChainId } from "../../state/chains/hooks";
import { useParams } from "react-router-dom";
import { PoolsStaticInfo } from "../../protocol/config";
import AddLquiditySection from "./components/AddLquiditySection";
import LoadingPage from "../../components/LoadingPage";
import TextWrapper from "../../theme/components/TextWrapper";
import theme from "../../theme";
import IconLoader from "../../theme/components/IconLoader";
import PageHeader from "../../theme/components/PageHeader";

const AddLiquidity = () => {
  const { poolName } = useParams<{ poolName: string }>();
  const [poolStaticInfo, setPoolStaticInfo] = useState<PoolsStaticInfo>(null);
  const chainId = useGetActiveChainId();

  const core = useCore();
  const isPoolValid: boolean = useMemo(() => {
    const pools = core.getLiquidityPools(chainId);
    const pool = pools.filter((info) => info.name === poolName)
    if (pool.length === 1) {
      setPoolStaticInfo(pool[0])
      return true
    } else {
      return false
    }
  }, [core, chainId, poolName]);

  if (!isPoolValid) {
    window.location.href = '/#/pools/';
    return <div />;
  }

  if (poolStaticInfo === null) {
    return (
      <LoadingPage />
    )
  }

  return (
    <div className={'custom-container'}>
      <div className={'m-b-20 single-line-center-start'}>
        <a href={'/#/pools'}>
          <TextWrapper text={'Pools'} Fcolor={theme.color.transparent[200]} />
        </a>
        <IconLoader iconName={'ArrowRight'} iconType={'arrow'} className={'m-r-4 m-l-4'} />
        <TextWrapper text={'Add Liquidity'} Fcolor={theme.color.transparent[100]} />
      </div>
      <PageHeader
        title={'ADD LIQUIDITY'}
        subTitle={'A simple interface for you to add liquidity into the main ARTH LP'}
      />
      <AddLquiditySection poolStaticInfo={poolStaticInfo} />
    </div>
  )
};

export default AddLiquidity;
