import React, { useState } from 'react';
import { useMediaQuery } from 'react-responsive';

import { FarmingConfigurations } from '../../../protocol/config';
import SuccessModal from '../../../theme/components/SuccesModal';
import { IFarmingApiDataState } from './FarmingCards';
import MobileRowCard from './MobileRowCard';

interface IProps {
  cardData: FarmingConfigurations;
  farmingApiData: IFarmingApiDataState;
}

const FarmingCard = (props: IProps) => {
  const pool = props.cardData;

  const [successModal, setSuccessModal] = useState(false);

  return (
    <div>
      <SuccessModal
        modalOpen={successModal}
        setModalOpen={() => setSuccessModal(false)}
        title={'Transaction Success!'}
        subsubTitle={'Your transaction is now being mined on the blockchain.'}
      />
      <MobileRowCard pool={pool} farmingApiData={props.farmingApiData} />
    </div>
  );
};

export default FarmingCard;
