import { BigNumber, Contract } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import ERC20 from '../../../protocol/ERC20';

import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';

import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  value:BigNumber;
};

export default (contractName: string) => {
  const [price, setPrice] = useState<State>({
    isLoading: true,
    value: BigNumber.from(0),
  });

  const core = useCore();
  const blockNumber = useBlockNumber();
  const activeChainId = useGetActiveChainId();

  const fetchValue = useCallback(async () => {
    const contract = core.contracts(contractName, activeChainId);
    setPrice({
      isLoading: false,
      value: await contract.get_virtual_price(),
    });
  }, [core, activeChainId, contractName]);

  useEffect(() => {
    fetchValue().catch((err) =>
      console.error(`Failed to fetch ${contractName} pool token price: ${err.stack}`),
    );
  }, [blockNumber, core, contractName, activeChainId, fetchValue]);

  return price;
};
