import { BigNumber } from '@ethersproject/bignumber';
import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import useCore from '../../useCore';
import { FarmingConfigurations } from "../../../protocol/config";

type State = {
  isLoading: boolean;
  value: BigNumber[];
};

export default (pool: FarmingConfigurations) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: [BigNumber.from(0)] });

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();
  const chainId = useGetActiveChainId();
  const stakingContract = pool.contract
  const erc20 = core.tokens(pool.depositToken, chainId);

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: [BigNumber.from(0)] });
      return;
    }

    const contract = core.contracts(stakingContract, chainId);
    if (pool.contract === 'ARTHu3PXBasicStakingV2') {
      const data = await contract.claimableReward(account, [erc20.address]);
      setValue({ isLoading: false, value: await data });
    } else if (pool.contract === 'ARTHuval3PSDotBasicStaking')  {
      const data = await contract.claimable(account, [erc20.address]);
      const dataFixed = [await data[0][0], await data[0][1]];
      setValue({ isLoading: false, value: dataFixed });
    } else {
      setValue({ isLoading: false, value: [await contract.earned(core.myAccount)] });
    }

  }, [account, core, stakingContract, chainId, pool.contract, erc20.address]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch staking balance: ${err}`));
    }
  }, [core.isUnlocked, account, blockNumber, fetchValue]);

  return value;
};
