import { getDisplayBalance } from "../../../utils/formatBalance";
import Input from "../../../theme/components/Input";
import CollateralDropDown from "../../../theme/components/CollateralDropDown";
import TextWrapper from "../../../theme/components/TextWrapper";
import theme from "../../../theme";
import InputContainer from "../../../theme/components/InputContainer";
import React, { useState } from "react";
import useCore from "../../../hooks/useCore";
import { useGetActiveChainId } from "../../../state/chains/hooks";
import useTokenBalance from "../../../hooks/state/useTokenBalance";
import { BigNumber } from "ethers";
import { parseUnits } from "ethers/lib/utils";

interface IProps {
  selectedToken: string;
  setTokenSupplyObj: (key: string, value: string) => void;
  setTokenBalanceObj: (key: string, value: BigNumber) => void;
}

const Inputs = (props: IProps) => {
  const {selectedToken, setTokenSupplyObj, setTokenBalanceObj} = props;
  const core = useCore();
  const chainId = useGetActiveChainId();
  const ERC20token = core.tokens(selectedToken, chainId);
  const tokenBalance = useTokenBalance(ERC20token);
  const [amount, setAmount] = useState<string>('');

  if (!tokenBalance.isLoading) {
    setTokenBalanceObj(selectedToken, tokenBalance.value);
  }

  return (
    <InputContainer
      label={'Enter Amount'}
      dataLabel={'Balance:'}
      dataValue={Number(getDisplayBalance(tokenBalance.value, ERC20token.decimal, 3)).toLocaleString()}
      className={'m-b-16'}
    >
      <div className={'single-line-center-between'}>
        <Input
          value={amount}
          setValue={(value) => {
            setAmount(value)
            setTokenSupplyObj(selectedToken, value);
          }}
          maxTag={true}
          onMaxClick={() => {
            setAmount(Number(getDisplayBalance(tokenBalance.value, ERC20token.decimal, 3)).toString());
            setTokenSupplyObj(selectedToken, Number(getDisplayBalance(tokenBalance.value, ERC20token.decimal, 3)).toString());
          }}
        />
        <CollateralDropDown
          selectedCollateral={selectedToken}
          hasDropDown={false}
        />
      </div>
      {
        tokenBalance.value.lt(BigNumber.from(parseUnits(amount || '0', ERC20token.decimal))) &&
          <TextWrapper
            text={`Input cannot be greater than your ${selectedToken} balance`}
            Fcolor={theme.color.red[300]}
            fontSize={12}
            className={'m-t-4'}
          />
      }
    </InputContainer>
  )
}

export default Inputs;
