import React, { useEffect, useState } from 'react';
import { useLocation, useParams } from 'react-router-dom';
import styled from 'styled-components';

import useCollateralForOpenTroves from '../../hooks/state/useCollateralForOpenTroves';
import Button from '../../theme/components/Button';
import IconLoader from '../../theme/components/IconLoader';
import PageHeader from '../../theme/components/PageHeader';

import Toggler from "./components/Toggler";
import YourLoans from "../yourLoans";
import AllLoans from "../AllLoans";
import useGetProtocolDebt from "../../hooks/state/useGetProtocolDebt";
import useGetProtocolCollateralInGMU from "../../hooks/state/useGetProtocolCollateralInGMU";
import SummaryBox from "./components/SummaryBox";
import { Grid } from "@material-ui/core";
import { getDisplayBalance } from "../../utils/formatBalance";
import Numeral from "numeral";
import config from "../../config";
import TextWrapper from "../../theme/components/TextWrapper";
import theme from "../../theme";
import { VfatLink } from "../Farming";
import { useGetActiveChainId } from "../../state/chains/hooks";
import StakedBanner from "../../components/StakedBanner";

type Tab = {
  index: number;
  key: string;
  text: string;
};

const allTabs: Tab[] = [
  {
    index: 0,
    key: 'overview',
    text: 'OVERVIEW',
  },
];

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

type IMode = 'your-loans' | 'all-loans';

const Loan = () => {
  const { mode } = useParams<{ mode: string }>();
  const chainId = useGetActiveChainId();
  const [selectedMode, setSelectedMode] = useState<IMode>('your-loans');
  const collateralForActiveTroves = useCollateralForOpenTroves();
  const entireSystemDebt = useGetProtocolDebt();
  const entireSystemCollateral = useGetProtocolCollateralInGMU();

  useEffect(() => {
    if (mode === 'your-loans' || mode === 'all-loans') {
      window.location.href = `/#/loans/${mode}`;
      setSelectedMode(mode)
    } else {
      window.location.href = `/#/loans/your-loans`;
      setSelectedMode('your-loans')
    }
  }, [mode]);

  return (
    <div className="custom-container">
      <VfatLink onClick={() => window.location.href = 'https://arth.mahadao.com/#/loans/all-loans'}>
        <TextWrapper
          text={'New'}
          Fcolor={theme.color.primary[300]}
          fontWeight={600}
          className={'m-r-12'}
        />
        <TextWrapper text={'To access the new ARTH implementation, visit here'} className={'m-r-12'} />
        <IconLoader iconName={'ArrowTailRight'} iconType={'arrow'} />
      </VfatLink>
      <div className="single-line-center-between mo-single-line-column">
        <PageHeader
          title={'LOANS'}
          subTitle={'Borrow ARTH against different collaterals at 0% interest.'}
          showLearnMore={true}
          learnMoreLink={'https://docs.arthcoin.com/protocol/introduction-arth-loans'}
        />
        <div className="single-line-center-start">
          <ButtonContainer>
            <Button
              to={'/loan/add'}
              tracking_id={'open_loan'}
              tracking_params={{ position: 'header' }}
            >
              <IconLoader iconName={'Add'} className="m-r-4" width={20} />
              Take Loan
            </Button>
          </ButtonContainer>
        </div>
      </div>

      <div>
        <Toggler
          mode={selectedMode}
          onModeChange={(data) => window.location.href = `/#/loans/${data}`} />
      </div>
      <StakedBanner />
      <div className="mo-custom-container m-b-40">
        <Grid container spacing={2}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <SummaryBox
              text={'TOTAL COLLATERAL LOCKED IN PROTOCOL'}
              isLoading={entireSystemCollateral.isLoading}
              value={Numeral(getDisplayBalance(entireSystemCollateral.value, 18, 3)).format('0,0.000') + ' $GMU'}
            />
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <SummaryBox
              text={'TOTAL ARTH DEBT OF PROTOCOL'}
              isLoading={entireSystemDebt.isLoading}
              value={Numeral(getDisplayBalance(entireSystemDebt.value, 18, 3)).format('0,0.000') + ' ARTH'}
            />
          </Grid>
        </Grid>
      </div>
      {selectedMode === 'your-loans' && <YourLoans />}
      {selectedMode === 'all-loans' && <AllLoans />}
    </div>
  );
};

export default Loan;

const ButtonContainer = styled.div`
        margin-bottom: 40px;
        @media (max-width: 600px) {
          max - width: max-content;
        padding-left: 16px;
        margin-top: -20px;
  }
        `;
