import React, { useState } from 'react';
import { useMediaQuery } from 'react-responsive';

import useLinkedListSize from '../../../hooks/state/sortedTroves/useGetLinkedListSize';
import useGetTotalCollateralRatio from '../../../hooks/state/troveManager/useGetTotalCollateralRatio';
import useGetCollateralWithGMUValue from '../../../hooks/state/useGetCollateralWithGMUValue';
import useGetTvlValueInUsd from '../../../hooks/state/useGetTvlValueInUsd';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { getDisplaycrParameters } from '../../../utils/crParameters';
import { troveDetails } from '../../../utils/tokenDetails';

import DesktopRowCard from './DesktopRowCard';
import MobileRowCard from './MobileRowCard';

type Props = {
  trove: string;
};

const SingleTrove = ({ trove }: Props) => {
  const isMobile = useMediaQuery({ maxWidth: '1280px' });
  const chainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, chainId);

  const onTroveClick = () => {
    window.location.href = `/#/loan/add/${trove}`;
  };

  const tvl = useGetCollateralWithGMUValue(troveDetail.depositToken, troveDetail.contractPrefix);
  const usdTvl = useGetTvlValueInUsd(troveDetail.depositToken, troveDetail.contractPrefix);
  const totalCollteralRatio = useGetTotalCollateralRatio(trove);
  const vaultLinkedListSize = useLinkedListSize(troveDetail.contractPrefix);
  const cr = getDisplaycrParameters(trove, chainId);

  return (
    <div>
     {/* {!isMobile ? (
        <DesktopRowCard
          trove={trove}
          onTroveClick={onTroveClick}
          tcr={totalCollteralRatio}
          cr={cr}
          tvl={tvl}
          usdTvl={usdTvl}
          vaultLinkedListSize={vaultLinkedListSize}
        />
      ) : (*/}
        <MobileRowCard
          trove={trove}
          onTroveClick={onTroveClick}
          tcr={totalCollteralRatio}
          cr={cr}
          tvl={tvl}
          usdTvl={usdTvl}
          vaultLinkedListSize={vaultLinkedListSize}
        />
      {/*)}*/}
    </div>
  );
};

export default SingleTrove;
