import React from 'react';
import { useMediaQuery } from 'react-responsive';

import useGetStabilityFeeAndToken from '../../../hooks/state/governance/useGetStabilityFee';
import useGetRedemptionFeeRateWithDecay from '../../../hooks/state/troveManager/useGetRedemptionFeeRateWithDecay';
import useGetCollateralWithGMUValue from '../../../hooks/state/useGetCollateralWithGMUValue';
import useGetTvlValueInUsd from '../../../hooks/state/useGetTvlValueInUsd';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { troveDetails } from '../../../utils/tokenDetails';

import DesktopRowCard from './DesktopRowCard';
import MobileRowCard from './MobileRowCard';

type Props = {
  trove: string;
};

const SingleTrove = ({ trove }: Props) => {
  const isMobile = useMediaQuery({ maxWidth: '1280px' });
  const chainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, chainId);

  const redemptionFeeRate = useGetRedemptionFeeRateWithDecay(troveDetail.contractPrefix);
  const stabiltyFee = useGetStabilityFeeAndToken(troveDetail.contractPrefix);
  const tvl = useGetCollateralWithGMUValue(troveDetail.depositToken, troveDetail.contractPrefix);
  const usdTvl = useGetTvlValueInUsd(troveDetail.depositToken, troveDetail.contractPrefix);

  const onTroveClick = () => {
    window.location.href = `/#/redeem/${trove}`;
  };

  return (
    <div>
      {/*{!isMobile ? ( art
        <DesktopRowCard
          trove={trove}
          onTroveClick={onTroveClick}
          redeemFee={redemptionFeeRate}
          stabilityFee={stabiltyFee}
          tvl={tvl}
          usdTvl={usdTvl}
        />
      ) : (*/}
        <MobileRowCard
          trove={trove}
          onTroveClick={onTroveClick}
          redeemFee={redemptionFeeRate}
          stabilityFee={stabiltyFee}
          tvl={tvl}
          usdTvl={usdTvl}
        />
      {/*)}*/}
    </div>
  );
};

export default SingleTrove;
