import React, { useEffect, useMemo } from "react";
import Button from "../../../theme/components/Button";
import ERC20 from "../../../protocol/ERC20";
import useApprove, { ApprovalState } from "../../../hooks/callbacks/useApprove";
import useCore from "../../../hooks/useCore";
import { useGetActiveChainId } from "../../../state/chains/hooks";

interface IProps {
  token: string,
  contract: string;
  updateStatus: (status: 'Approved' | 'IsApproving' | 'NotApproved') => void;
}

const ApproveButtons = (props: IProps) => {
  const { token, updateStatus, contract } = props;
  const core = useCore();
  const activeChainId = useGetActiveChainId();

  const [approveStatus, approveCollateral] = useApprove(
    core.tokens(token, activeChainId),
    core.contracts(contract, activeChainId).address,
  );

  const isCollatApproved = useMemo(() => approveStatus === ApprovalState.APPROVED, [approveStatus]);
  const isCollatApproving = useMemo(() => approveStatus === ApprovalState.PENDING, [approveStatus]);

  useEffect(() => {
    if (approveStatus === ApprovalState.PENDING) {
      return updateStatus('IsApproving')
    } else if (approveStatus === ApprovalState.APPROVED) {
      return updateStatus('Approved')
    } else {
      return updateStatus('NotApproved');
    }
  }, [approveStatus, updateStatus])

  return (
    <Button
      disabled={isCollatApproved || isCollatApproving}
      loading={isCollatApproving}
      onClick={approveCollateral}
      text={isCollatApproved ? `${token} Approved` : `Approve ${token}`}
      tracking_id={`${contract}_approve_collateral`}
      tracking_params={{
        collateral: token,
      }}
    />
  )
}

export default ApproveButtons;
