import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

import { useGetActiveChainId } from './state/chains/hooks';
import NoPageFound from './theme/components/NoPageFound';
import Page from './theme/components/Page/Page';
import AddLoan from './views/AddLoan';
import AddStrategies from './views/AddStrategies';
import Farming from './views/Farming';
import Faucet from './views/Faucet';
import Loan from './views/Loan';
import Pools from './views/Pools';
import Positions from './views/Positions';
import Redeem from './views/Redeem';
import SelectLoan from './views/SelectLoan';
import SelectRedeem from './views/SelectRedeem';
import StabilityPool from './views/StabilityPool';
import Strategies from './views/Strategies';
import Tokenization from './views/Tokenization';
import LoanDetails from "./views/LoanDetails";
import ModifyLoan from "./views/ModifyLoan";
import AddLiquidity from "./views/AddLiquidity";
import Migration from "./views/Migration";
import Trade from "./views/Trade";
import ComingSoon from "./components/ComingSoon";
import ProtocolSuspended from "./components/ProtocolSuspended";

const Navigation = () => {
  const activeChainId = useGetActiveChainId();
  const defaultUrl = '/loans/all-loans'

  // if (activeChainId === 56) {
  //   return (
  //     <Switch>
  //       <Route path="/farming">
  //         <Page>
  //           <Farming />
  //         </Page>
  //       </Route>
  //       <Route exact path="*">
  //         <Page>
  //           <ProtocolSuspended />
  //         </Page>
  //       </Route>
  //     </Switch>
  //   )
  // }

  return (
    <Switch>
      <Route exact path="/">
        <Redirect to={defaultUrl} />
      </Route>
      <Route path="/loans/:mode">
        <Page>
          <Loan />
        </Page>
      </Route>
      <Route path="/loan/add/:id">
        <Page>
          <AddLoan />
        </Page>
      </Route>
      <Route path="/loan/add">
        <Page>
          <SelectLoan />
        </Page>
      </Route>
      <Route path="/loan/details/:ownerAddress/:trove">
        <Page>
          <LoanDetails />
        </Page>
      </Route>
      <Route path="/loan/modify/:trove">
        <Page>
          <ModifyLoan />
        </Page>
      </Route>

      <Route path="/redeem/:id">
        <Page>
          <Redeem />
        </Page>
      </Route>
      <Route path="/redeem">
        <Page>
          <SelectRedeem />
        </Page>
      </Route>
      <Route path="/stability-pool">
        <Page>
          <StabilityPool />
        </Page>
      </Route>
      <Route path="/tokenization">
        <Page>
          <Tokenization />
        </Page>
      </Route>
      <Route path="/migration">
        <Page>
          <Migration />
        </Page>
      </Route>
      {/*<Route path="/position">
        <Page>
          <Positions />
        </Page>
      </Route>*/}
      <Route path="/pools/add-liquidity/:poolName">
        <Page>
          {(activeChainId === 137 || activeChainId === 56)
            ? <AddLiquidity />
            : <ComingSoon />
          }
        </Page>
      </Route>
      <Route path="/pools">
        <Page>
          {(activeChainId === 137 || activeChainId === 56)
            ? <Pools />
            : <ComingSoon />
          }
        </Page>
      </Route>
      <Route path="/trade">
        <Page>
          {(activeChainId === 137 || activeChainId === 56)
            ? <Trade />
            : <ComingSoon />
          }
        </Page>
      </Route>
      <Route path="/faucet">
        {activeChainId === 97 || activeChainId === 80001 ? (
          <Page>
            <Faucet />
          </Page>
        ) : (
          <Redirect to={defaultUrl} />
        )}
      </Route>
      <Route path="/farming">
        <Page>
          <Farming />
        </Page>
      </Route>
      <Route exact path="*">
        <NoPageFound />
      </Route>
    </Switch>
  );
};

export default Navigation;
