import React, { useState } from 'react';
import { useMediaQuery } from 'react-responsive';

import useETHGain from '../../../hooks/state/stabilityPool/useETHGain';
import useGetDepositedAmount from '../../../hooks/state/stabilityPool/useGetDepositedAmount';
import useGetPoolDeposit from '../../../hooks/state/stabilityPool/useGetPoolDeposit';
import useGetPoolShare from '../../../hooks/state/stabilityPool/useGetPoolShare';
import useGetRemainingETHGain from '../../../hooks/state/stabilityPool/useGetRemainingETHGain';
import useGetTvlInARTH from '../../../hooks/state/useGetTvlInARTH';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { troveDetails } from '../../../utils/tokenDetails';
import ConfirmClaimModal from '../modal/ConfirmClaimModal';
import StakeArthModal from '../modal/StakeArthModal';
import WithdrawARTHModal from '../modal/WithdrawARTHModal';

import DesktopRowCard from './DesktopRowCard';
import MobileRowCard from './MobileRowCard';

type Props = {
  trove: string;
};

const SinglePool = ({ trove }: Props) => {
  const [openStakeModal, setOpenStakeModal] = useState<boolean>(false);
  const [openConfirmClaimModal, setOpenConfirmClaimModal] = useState<boolean>(false);
  const [openConfirmWithdrawModal, setOpenConfirmWithdrawModal] = useState<boolean>(false);

  const isMobile = useMediaQuery({ maxWidth: '1280px' });
  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);

  const ethGain = useETHGain(trove);
  const poolShare = useGetPoolShare(trove);
  const remainingETH = useGetRemainingETHGain(trove);
  const poolDeposit = useGetPoolDeposit(trove);
  const depositedStake = useGetDepositedAmount(trove);
  const arthTvl = useGetTvlInARTH(troveDetail.contractPrefix);

  return (
    <div>
      {/* {!isMobile ? (
        <DesktopRowCard
          ethGain={ethGain}
          poolShare={poolShare}
          remainingETH={remainingETH}
          depositedStake={depositedStake}
          tvl={arthTvl}
          trove={trove}
          onDeposit={() => setOpenStakeModal(true)}
          onWithdrawal={() => setOpenConfirmWithdrawModal(true)}
          onClaim={() => setOpenConfirmClaimModal(true)}
        />
      ) : (*/}
      <MobileRowCard
        ethGain={ethGain}
        poolShare={poolShare}
        remainingETH={remainingETH}
        trove={trove}
        poolDeposit={poolDeposit}
        depositedStake={depositedStake}
        tvl={arthTvl}
        onDeposit={() => setOpenStakeModal(true)}
        onWithdrawal={() => setOpenConfirmWithdrawModal(true)}
        onClaim={() => setOpenConfirmClaimModal(true)}
      />
      {/*)}*/}
      <StakeArthModal
        unClaimedReward={ethGain}
        trove={trove}
        openModal={openStakeModal}
        onClose={() => setOpenStakeModal(false)}
      />
      <WithdrawARTHModal
        unClaimedReward={ethGain}
        depositedStake={depositedStake}
        trove={trove}
        openModal={openConfirmWithdrawModal}
        onClose={() => setOpenConfirmWithdrawModal(false)}
      />
      <ConfirmClaimModal
        openModal={openConfirmClaimModal}
        collateralPool={trove}
        onClose={() => setOpenConfirmClaimModal(false)}
      />
    </div>
  );
};

export default SinglePool;
