import React, { useMemo, useState } from "react";
import { formatToBN, getDisplayBalance } from "../../../utils/formatBalance";
import Input from "../../../theme/components/Input";
import CollateralDropDown from "../../../theme/components/CollateralDropDown";
import TextWrapper from "../../../theme/components/TextWrapper";
import theme from "../../../theme";
import InputContainer from "../../../theme/components/InputContainer";
import useTokenBalance from "../../../hooks/state/useTokenBalance";
import useCore from "../../../hooks/useCore";
import { useGetActiveChainId } from "../../../state/chains/hooks";
import IconLoader from "../../../theme/components/IconLoader";
import Grid from "@material-ui/core/Grid";
import Button from "../../../theme/components/Button";
import DataField from "../../../theme/components/DataField";

import useGetUnderlyingOutput from '../../../hooks/state/useGetUnderlyingOutput';
import { BigNumber } from "ethers";
import { parseUnits } from "ethers/lib/utils";
import useApprove, { ApprovalState } from '../../../hooks/callbacks/useApprove';
import useBuyARTHusdForToken from "../../../hooks/callbacks/useBuyARTHusdForToken";
import useBuyARTHForToken from "../../../hooks/callbacks/useBuyARTHForToken";
import InfoTip from "../../../theme/components/InfoTip";
import { useSlippage } from "../../../state/slippage/hooks";

const BuyContent = () => {
  const core = useCore();
  const chainId = useGetActiveChainId();

  const [tokenAValue, setTokenAValue] = useState<string>('');
  const listOfATokens = core.getTradingCollateralTypes(chainId);
  const [tokenA, setTokenA] = useState<string>(listOfATokens[0]);
  const tokenAERC20 = core.tokens(tokenA, chainId);
  const tokenABalance = useTokenBalance(tokenAERC20);
  const [openADropDown, setOpenADropDown] = useState<boolean>(false);

  const [tokenBValue, setTokenBValue] = useState<string>('');
  const [tokenB, setTokenB] = useState<string>('ARTH');
  const listOfBTokens = ['ARTH', 'ARTH.usd'];
  const tokenBERC20 = core.tokens(tokenB, chainId);
  const tokenBBalance = useTokenBalance(tokenBERC20);
  const [openBDropDown, setOpenBDropDown] = useState<boolean>(false);
  const { value } = useSlippage();
  const slippage = value;

  if (!tokenAERC20 || !tokenBERC20) {
    window.location.reload();
  }

  const outputFromSwap = useGetUnderlyingOutput(
    core.getPool(chainId).minterContract,
    core.getCRVTradeTokenIndex(tokenA, chainId),
    0,
    tokenAValue,
    tokenAERC20.decimal
  );

  const underlyingOutput = useMemo(() => {
    if (tokenB === 'ARTH.usd') return outputFromSwap;
    return { ...outputFromSwap, value: outputFromSwap.value.mul(1e6).div(2e6)};
  }, [tokenB, outputFromSwap]);

  const priceFromSwap = useGetUnderlyingOutput(
    core.getPool(chainId).minterContract,
    core.getCRVTradeTokenIndex(tokenA, chainId),
    0,
    '1',
    tokenAERC20.decimal
  );

  const price = useMemo(() => {
    if (tokenB === 'ARTH.usd') return priceFromSwap;
    return { ...priceFromSwap, value: priceFromSwap.value.mul(1e6).div(2e6)};
  }, [tokenB, priceFromSwap]);

  const [approveStatus, approveCollateral] = useApprove(
    tokenAERC20,
    core.contracts('SwapRouter', chainId).address,
  );

  const buyHandler = useBuyARTHusdForToken(
    tokenA,
    BigNumber.from(parseUnits(tokenAValue || '0', tokenAERC20.decimal)),
    outputFromSwap.value
  );

  const buyARTHHandler = useBuyARTHForToken(
    tokenA,
    BigNumber.from(parseUnits(tokenAValue || '0', tokenAERC20.decimal)),
    outputFromSwap.value
  );

  const isCollatApproved = useMemo(() => approveStatus === ApprovalState.APPROVED, [approveStatus]);
  const isCollatApproving = useMemo(() => approveStatus === ApprovalState.PENDING, [approveStatus]);

  const priceImpact = useMemo(() => {
    const tokenAValueInNum = Number(tokenAValue);
    if (outputFromSwap.value.lte(0) || !tokenAValueInNum) return 0;
    const outputValueInNum = Number(getDisplayBalance(outputFromSwap.value, 18, 18));
    return 100 - (outputValueInNum * 100  / tokenAValueInNum);
  }, [tokenAValue, outputFromSwap]);

  const isInputGreaterThanBalance = useMemo(() => {
    return tokenABalance.value.lt(formatToBN(tokenAValue, tokenAERC20.decimal));
  }, [tokenABalance, tokenAValue, tokenAERC20]);

  const isButtonDisabled = useMemo(() => {
    return isInputGreaterThanBalance ||
      !Number(tokenAValue) ||
      outputFromSwap.value.lte(0)
  }, [isInputGreaterThanBalance, tokenAValue, outputFromSwap]);

  return (
    <div className={'custom-mahadao-container-content'}>
      <div>
        <InputContainer
          label={'Enter Amount'}
          dataLabel={'Balance:'}
          dataValueLoading={tokenABalance.isLoading}
          dataValue={`${Number(
            getDisplayBalance(tokenABalance.value, tokenAERC20.decimal, 6),
          ).toLocaleString('en-US', { minimumFractionDigits: 3 })}`}
        >
          <div className="single-line-center-between">
            <Input
              disabled={tokenABalance.isLoading}
              value={tokenAValue}
              setValue={(value) => {
                setTokenAValue(value);
              }}
              maxTag={true}
              onMaxClick={() => {
                setTokenAValue(Number(getDisplayBalance(tokenABalance.value, tokenAERC20.decimal, 6)).toString());
              }}
            />
            <CollateralDropDown
              selectedCollateral={tokenA}
              hasDropDown={true}
              collateralTypes={listOfATokens}
              showDropDown={openADropDown}
              toggleDropDown={() => setOpenADropDown(!openADropDown)}
              ondropDownValueChange={(data) => {
                setTokenA(data);
                setOpenADropDown(false)
              }}
            />
          </div>
          {
            isInputGreaterThanBalance && <TextWrapper
              text={`Input cannot be greater than your ${tokenA} balance`}
              Fcolor={theme.color.red[300]}
              fontSize={12}
              className={'m-t-4'}
            />
          }
        </InputContainer>
      </div>
      <div className={'single-line-center-center'}>
        <IconLoader iconName={'ArrowTailDown'} iconType={'arrow'} className={'p-t-12 p-b-12'}/>
      </div>
      <div>
        <InputContainer
          label={'Enter Amount'}
          dataLabel={'Balance:'}
          dataValueLoading={tokenBBalance.isLoading}
          dataValue={`${Number(
            getDisplayBalance(tokenBBalance.value, tokenBERC20.decimal, 6),
          ).toLocaleString('en-US', { minimumFractionDigits: 3 })}`}
        >
          <div className="single-line-center-between">
            <Input
              disabled={true}
              value={
                Number(tokenAValue)
                  ? getDisplayBalance( underlyingOutput.value, tokenBERC20.decimal, 6)
                  : tokenAValue === '' ? '' : '0'
              }
              setValue={(value) => {
                setTokenBValue(value);
              }}
              maxTag={false}
            />
            <CollateralDropDown
              selectedCollateral={tokenB}
              hasDropDown={true}
              collateralTypes={listOfBTokens}
              showDropDown={openBDropDown}
              toggleDropDown={() => setOpenBDropDown(!openBDropDown)}
              ondropDownValueChange={(data) => {
                setTokenB(data);
                setOpenBDropDown(false)
              }}
            />
          </div>
        </InputContainer>
      </div>
      <div className={'m-t-24'}>
        <div className={'single-line-center-between m-b-8'}>
          <TextWrapper
            text={'Price'}
            Fcolor={theme.color.transparent[100]}
          />
          <div className={'single-line-center-end'}>
            <TextWrapper
              text={Number(getDisplayBalance(price.value, 18, 3)).toLocaleString()}
              className={'m-r-4'}
            />
            <div className={'tags m-r-4'}>
              <TextWrapper
                text={tokenB}
                fontWeight={600}
                Fcolor={theme.color.transparent[100]}
              />
            </div>
            <TextWrapper
              text={'1'}
              className={'m-r-4'}
            />
            <div className={'tags m-r-4'}>
              <TextWrapper
                text={tokenA}
                fontWeight={600}
                Fcolor={theme.color.transparent[100]}
              />
            </div>
          </div>
        </div>
        <DataField
          label={'Price Impact'}
          value={priceImpact.toLocaleString() + '%'}
        />
      </div>
      <div className={'m-t-32'}>
        {(Number(slippage) < 0.5) &&
          <InfoTip
            type={"Warning"}
            msg={"Your transactions might fail for the current slippage. Increase slippage to avoid transaction failure"}
            className={"m-b-16"}
          />
        }
        <Grid container spacing={2} direction={'row'}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              text={`Approve ${tokenA}`}
              onClick={approveCollateral}
              disabled={
                isCollatApproved ||
                isButtonDisabled
              }
              loading={isCollatApproving}
              tracking_id={'approve_buy'}
              tracking_params={{
                collateral: tokenA,
                amount: Number(tokenAValue) || 0,
              }}
            />
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              text={'Buy'}
              disabled={!isCollatApproved || isButtonDisabled}
              onClick={tokenB === 'ARTH.usd' ? buyHandler : buyARTHHandler}
              tracking_id={'buy'}
              tracking_params={{
                action: 'confirm',
                collateral: tokenA,
                amount: Number(tokenAValue) || 0,
              }}
            />
          </Grid>
        </Grid>
      </div>
    </div>
  )
}

export default BuyContent;
