import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import { useBlockNumber } from '../../state/application/hooks';
import { useGetActiveChainId } from '../../state/chains/hooks';
import useCore from '../useCore';

type State = {
  isLoading: boolean;
  value: Array<string>;
};

export default () => {
  const [value, setValue] = useState<State>({ isLoading: true, value: [] });

  const core = useCore();
  const activeChainId = useGetActiveChainId();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: [] });
      return;
    }

    const collaterals = core.getAllTroves(activeChainId);

    // Fetch respective trove.
    const collateralsTrove = collaterals.map(async (colalteral: string) => {
      const contract = core.getSortedTroves(colalteral, activeChainId);
      return await contract.contains(account);
    });
    const troves = await Promise.all(collateralsTrove);

    // Fetch all collaterals with active trove.
    const activeTroveCollaterals: string[] = [];
    troves.forEach((trove: boolean, i: number) => {
      if (trove) {
        activeTroveCollaterals.push(collaterals[i]);
      }
    });

    setValue({ isLoading: false, value: activeTroveCollaterals });
  }, [account, core, activeChainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch arth in circulating supply with %: ${err}`),
      );
    }
  }, [core.isUnlocked, account, core.myAccount, blockNumber, fetchValue]);

  return value;
};
