import React, { useEffect, useState } from 'react';
import styled from 'styled-components';

import Grid from '@material-ui/core/Grid';

import SlippageContainer from '../../components/SlippageContainer';
import PageHeader from '../../theme/components/PageHeader';
import TextWrapper from '../../theme/components/TextWrapper';

import BuyContent from './components/BuyContent';
import SellContent from './components/SellContent';

const Trade = () => {
  useEffect(() => window.scrollTo(0, 0), []);

  const [type, setType] = useState<'Buy' | 'Sell'>('Buy');

  return (
    <div className="custom-container">
      <PageHeader
        title={'TRADE'}
        subTitle={'Buy/sell large amounts of ARTH to any stablecoin at the lowest slippage.'}
        // showLearnMore={true}
        // learnMoreLink={'https://mahadao.com'}
        className="m-b-40"
      />
      <Grid container spacing={2} className="m-b-24">
        <Grid item lg={6} md={12} sm={12} xs={12}>
          <div className={'custom-mahadao-container'}>
            <LeftTopCardHeader className={'custom-mahadao-container-header'}>
              <div style={{ display: 'flex', flex: '1' }}>
                <TabContainer onClick={() => setType('Buy')}>
                  {type === 'Buy' && <ActiveTab />}
                  <TextWrapper text={'Buy'} fontWeight={600} />
                </TabContainer>
                <TabContainer onClick={() => setType('Sell')}>
                  {type === 'Sell' && <ActiveTab />}
                  <TextWrapper text={'Sell'} fontWeight={600} />
                </TabContainer>
              </div>
              <SlippageContainer />
            </LeftTopCardHeader>
            {type === 'Buy' && (
              <BuyContent />
            )}
            {type === 'Sell' && (
              <SellContent />
            )}
          </div>
        </Grid>
      </Grid>
    </div>
  );
};

const LeftTopCardHeader = styled.div`
  display: flex;
  flex-direction: row;
`;

const TabContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px 12px;
  width: 100px;
  height: 80px;
  z-index: 1;
  cursor: pointer;
  position: relative;
`;

const ActiveTab = styled.div`
  position: absolute;
  width: 100%;
  padding: 32px 12px;
  background: transparent;
  height: 80px;
  z-index: 0;
  border-bottom: 2px solid #fd5656;
`;

export default Trade;
