import React, { CSSProperties } from 'react';
import styled from 'styled-components';

import theme from '../../index';
import { BackgroundAbsolute } from '../CustomComponent';
import IconLoader from '../IconLoader';
import TextWrapper from '../TextWrapper';

interface Iprops {
  openSelector: boolean;
  toggleSelector: () => void;

  dropdownValue: string[];

  selectedData: string;
  setSelectedData: (data: string) => void;

  symbolicData?: boolean;

  dontShowBackground?: boolean;
  position?: {
    top: string;
    bottom: string;
    left: string;
    right: string;
  };
  className?: string;
}

const Selector = (props: Iprops) => {
  const {
    openSelector,
    toggleSelector,

    dropdownValue,

    selectedData,
    setSelectedData,

    symbolicData = false,

    dontShowBackground = false,
    position = {
      top: '50px',
      bottom: '',
      left: '',
      right: '0px',
    },
    className = '',
  } = props;

  const IContainerStyle = () => {
    let returnObj: CSSProperties = {};
    if (dontShowBackground) {
      returnObj['padding'] = '0px';
      returnObj['backgroundColor'] = 'transparent';
    }
    return returnObj;
  };

  return (
    <SelectorContainer
      onClick={() => toggleSelector()}
      style={IContainerStyle()}
      className={className}
    >
      {symbolicData && (
        <IconLoader
          iconName={selectedData}
          iconType="tokenSymbol"
          className="m-r-8"
          width={24}
        />
      )}
      <div className={'content-width'}>
        <TextWrapper text={selectedData} fontWeight={600} fontSize={14} />
      </div>
      <IconLoader
        iconName={openSelector ? 'ArrowUp' : 'ArrowDown'}
        iconType={'arrow'}
        className="m-l-12"
      />
      {openSelector && (
        <BackgroundAbsolute
          onClick={() => {
            toggleSelector();
          }}
        />
      )}
      {openSelector && (
        <CustomDropDownContainer position={position}>
          {dropdownValue?.map((item, index) => {
            return (
              <CustomDropDownLi
                onClick={() => {
                  if (setSelectedData) setSelectedData(item);
                }}
                key={index}
              >
                <div className="single-line-center-start">
                  {symbolicData && (
                    <IconLoader
                      iconName={item}
                      iconType="tokenSymbol"
                      className="m-r-8"
                      width={24}
                    />
                  )}
                  <TextWrapper text={item} />
                </div>
              </CustomDropDownLi>
            );
          })}
        </CustomDropDownContainer>
      )}
    </SelectorContainer>
  );
};

export default Selector;

const SelectorContainer = styled.div`
  padding: 12px 20px;
  background: rgba(255, 255, 255, 0.08);
  border-radius: 6px;
  width: max-content;
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
  @media (max-width: 600px) {
  }
`;

interface CustomDropDownContainerProps {
  position: {
    top: string;
    bottom: string;
    left: string;
    right: string;
  };
}

const CustomDropDownContainer = styled.div<CustomDropDownContainerProps>`
  position: absolute;
  top: ${({ position }) => position.top};
  bottom: ${({ position }) => position.bottom};
  right: ${({ position }) => position.right};
  left: ${({ position }) => position.left};
  z-index: 111;
  background: ${theme.color.dark[300]};
  border-radius: 6px;
  min-width: max-content;
  overflow: hidden;
`;

const CustomDropDownLi = styled.div`
  display: flex;
  flex-direction: row;
  padding: 12px 20px;
  align-items: center;
  cursor: pointer;

  &:hover {
    background: rgba(255, 255, 255, 0.08);
  }
`;
