import React, { useState } from 'react';

import Button from '../../theme/components/Button';
import IconLoader from '../../theme/components/IconLoader';
import TextWrapper from '../../theme/components/TextWrapper';

import YourLoansTable from './components/YourLoansTable';
import useGetAllOpenTroves from "../../hooks/state/useGetAllOpenTroves";
import InfoTip from "../../theme/components/InfoTip";

const YourLoans = () => {
  const [doYouLoanRefresh, setDoYourLoanRefresh] = useState<boolean>(false);
  const troves = useGetAllOpenTroves(false);

  return (
    <div>
      <div className="m-t-20 m-b-40">
        <div className="m-b-24 mo-custom-container single-line-center-between">
          {/* <div/> */}
          <div style={{ maxWidth: '200px' }}>
            <Button
              variant={'transparent'}
              onClick={() => setDoYourLoanRefresh(!doYouLoanRefresh)}
              tracking_id={'refresh_your_loans'}
            >
              <IconLoader iconName={'Sync'} className="m-r-8" />
              <TextWrapper text={'Refresh'} fontWeight={600} />
            </Button>
          </div>
        </div>
        {troves.value.length > 0 && <InfoTip
          type={"Info"}
          msg={
            <div>
              Stake tokens in farming pools and get apr upto 120%&nbsp;
              <a className={"links"} href={"/#/farming"}>Stake</a>
            </div>
          }
          className={"m-b-24"}
        />}
        <YourLoansTable doRefresh={doYouLoanRefresh} />
      </div>
    </div>
  );
};

export default YourLoans;
