import { BigNumber } from 'ethers';
import { useCallback } from 'react';
import { useWallet } from 'use-wallet';

import { useAddPopup } from '../../state/application/hooks';
import { useGetActiveChainId } from '../../state/chains/hooks';
import { useTransactionAdder } from '../../state/transactions/hooks';
import { getDisplayBalance } from '../../utils/formatBalance';
import formatErrorMessage from '../../utils/formatErrorMessage';
import useCore from '../useCore';
import { useSlippage } from '../../state/slippage/hooks';

export default function (
  symbol: string,
  amountIn: BigNumber,
  amountOut: BigNumber
) {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const chainId = useGetActiveChainId();
  const slippage = useSlippage();

  const action = useCallback(
    async (callback?: () => void): Promise<void> => {
      const contract = core.contracts('SwapRouter', chainId);

      const slippageRounded = Math.floor(slippage.value * 1e3) / 1e3;
      const slippageAdjustedAmountout = !Number(slippage.value)
        ? amountOut.mul(99).div(100)
        : amountOut.sub(amountOut.mul(slippageRounded * 1e3).div(1e5))
      
      const token = chainId === 137 ? 'DAI' : 'BUSD'

      try {
        const response = await contract.buyARTHUSDForExact(
          symbol === token ? amountIn : BigNumber.from(0),
          symbol === 'USDC' ? amountIn : BigNumber.from(0),
          symbol === 'USDT' ? amountIn : BigNumber.from(0),
          symbol === token ? slippageAdjustedAmountout : BigNumber.from(0),
          symbol === 'USDC' ? slippageAdjustedAmountout : BigNumber.from(0),
          symbol === 'USDT' ? slippageAdjustedAmountout : BigNumber.from(0),
          core.myAccount,
          Math.floor(Date.now() / 1000) + 5 * 60
        );

        addTransaction(response, {
          summary: `Buy ARTH.usd for ${
            Number(getDisplayBalance(amountIn, core.tokens(symbol, chainId).decimal)).toLocaleString()
          } ${symbol.toUpperCase()}`,
        });

        if (callback) callback();
      } catch (e) {
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
      }
    },
    [core, chainId, addTransaction, slippage.value, addPopup, amountIn,
    amountOut, symbol],
  );

  return action;
}
