import React from 'react';
import Loader from 'react-spinners/BeatLoader';

import TextWrapper from '../../../theme/components/TextWrapper';

interface Iprops {
  text: string;
  value: string;
  isLoading: boolean;
}

const SummaryBox = (props: Iprops) => {
  const { text, value, isLoading } = props;

  return (
    <div className="material-primary text-center">
      <div className="m-b-4">
        <TextWrapper text={text} fontSize={16} align={'center'} />
      </div>
      {isLoading ? (
        <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
      ) : (
        <TextWrapper text={value} fontSize={24} fontWeight={'bold'} align={'center'} />
      )}
    </div>
  );
};

export default SummaryBox;
