import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useWallet } from 'use-wallet';

import { AppState } from '../../state';
import { useBlockNumber } from '../../state/application/hooks';
import { useGetActiveChainId } from '../../state/chains/hooks';
import useCore from '../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default (token: string, contractPrefix: string) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const { account } = useWallet();
  const activeChainId = useGetActiveChainId();
  const erc20Token = core.tokens(token, activeChainId);
  // const gmuPrice = useSelector((state: AppState) => state.analytics.gmuPrice);

  const fetchValue = useCallback(async () => {
    if (!account && !erc20Token) {
      setValue({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    const contract = core.getPriceFeed(token, activeChainId);
    const price: BigNumber = await contract.callStatic.fetchPrice();
    const calcValue =
      price.div(BigNumber.from(10).pow(18 - erc20Token.decimal)) || BigNumber.from(0);

    if (!value.value.eq(calcValue)) {
      setValue({
        isLoading: false,
        value: calcValue,
      });
    }

  }, [account, activeChainId, core, erc20Token.decimal, token, value.value]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch price: ${err}`));
    }
  }, [core.isUnlocked, fetchValue]);

  return value;
};
