import React from 'react';
import styled from 'styled-components';

interface IProps {
  mode: 'your-loans' | 'all-loans';
  onModeChange: (id: 'your-loans' | 'all-loans') => void;
}

const Toggler = (props: IProps) => {
  const { mode, onModeChange } = props;

  let headerList: { id: 'your-loans' | 'all-loans'; name: string }[] = [
    {
      id: 'your-loans',
      name: 'Your Loans',
    },
    {
      id: 'all-loans',
      name: 'All Loans',
    },
  ];
  return (
    <div
      style={{ position: 'relative', display: 'flex' }}
      className="mo-custom-container m-b-24"
    >
      <ScrollDiv style={{ overflowX: 'scroll' }}>
        <ButtonGroupDiv>
          {headerList.map((obj) => (
            <TextDiv
              style={
                mode === obj.id
                  ? {
                    background: mode === obj.id ? '#423B38' : 'transparent',
                    border: '1px solid rgba(255, 255, 255, 0.08)',
                  }
                  : {}
              }
              key={obj?.id}
              onClick={() => {
                onModeChange(obj.id);
              }}
            >
              <Text1>{obj?.name}</Text1>
            </TextDiv>
          ))}
        </ButtonGroupDiv>
      </ScrollDiv>
    </div>
  );
};

export default Toggler;

const ButtonGroupDiv = styled.div`
  background: #2a2827;
  border-radius: 4px;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 6px 0;
  flex-wrap: wrap;
  width: max-content;
`;

const TextDiv = styled.div`
  height: 40px;
  display: inline-flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  border-radius: 4px;
  margin: 0 6px;
  padding: 0 12px;
  cursor: pointer;

  &:hover {
    background: #423b3860;
  }
`;

const ScrollDiv = styled.div`
  ::-webkit-scrollbar {
    height: 0;
    background: transparent; /* make scrollbar transparent */
  }
`;

const Text1 = styled.p`
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
  margin: 0;
`;
