import { useCallback } from 'react';

import { useAddPopup } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { useTransactionAdder } from '../../../state/transactions/hooks';
import formatErrorMessage from '../../../utils/formatErrorMessage';
import useCore from '../../useCore';
import * as Sentry from "@sentry/react";
import { useWallet } from "use-wallet";
import { FarmingConfigurations } from "../../../protocol/config";
import { BNZERO } from "../../../protocol/constants";

export default function (pool: FarmingConfigurations) {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const chainId = useGetActiveChainId();
  const {account} = useWallet();
  const stakingContract = pool.contract
  const erc20 = core.tokens(pool.depositToken, chainId);

  const action = useCallback(
    async (callback?: () => void): Promise<void> => {
      const contract = core.contracts(stakingContract, chainId);

      try {
        const response =
          stakingContract === 'ARTHu3PXBasicStakingV2'
            ? await contract.claim(account, [erc20.address])
            : stakingContract === 'ARTHuval3PSDotBasicStaking'
              ? await contract.claim(account, [erc20.address], BNZERO)
              : await contract.getReward()

        if (response.status !== 1) {
          Sentry.captureException({
            ...response,
            stakingContract,
          });
        }

        addTransaction(response, {
          summary: `Claim Rewards`,
        });

        if (callback) callback();
      } catch (e) {
        Sentry.captureException({
          ...e,
          stakingContract,
        });
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e.stack,
          },
        });
      }
    },
    [addPopup, stakingContract, core, addTransaction],
  );

  return action;
}
