import { BigNumber, Contract } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import ERC20 from '../../../protocol/ERC20';

import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';

import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  value:BigNumber;
};

export default (poolContractName: string, amountIn1: BigNumber, amountIn2: BigNumber, amountIn3: BigNumber, amountIn4: BigNumber) => {
  const [amountOut, setAmountOut] = useState<State>({
    isLoading: true,
    value: BigNumber.from(0),
  });

  const core = useCore();
  const blockNumber = useBlockNumber();
  const activeChainId = useGetActiveChainId();

  const fetchValue = useCallback(async () => {
    const contract = core.contracts('ZapDepositor', activeChainId);
    const poolContract = core.contracts(poolContractName, activeChainId);

    const amountOut = await contract.calc_token_amount(
      poolContract.address,
      [
        amountIn1,
        amountIn2,
        amountIn3,
        amountIn4
      ],
      true
    );
    
    setAmountOut({
      isLoading: false,
      value: amountOut,
    });
  }, [
    core, 
    activeChainId, 
    poolContractName, 
    amountIn1,
    amountIn2,
    amountIn3,
    amountIn4
  ]);

  useEffect(() => {
    fetchValue().catch((err) =>
      console.error(`Failed to fetch out lp tokens amount: ${err.stack}`),
    );
  }, [
    blockNumber, 
    core, 
    poolContractName, 
    activeChainId, 
    fetchValue,
    amountIn1,
    amountIn2,
    amountIn3,
    amountIn4
  ]);

  return amountOut;
};
