import { BigNumber } from 'ethers';
import Numeral from 'numeral';
import React from 'react';
import Loader from 'react-spinners/BeatLoader';
import styled from 'styled-components';

import { useGetActiveChainId } from '../../../state/chains/hooks';
import theme from '../../../theme';
import Button from '../../../theme/components/Button';
import IconLoader from '../../../theme/components/IconLoader';
import TextWrapper from '../../../theme/components/TextWrapper';
import ToolTip from '../../../theme/components/Tooltip';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { BasicState } from '../../../utils/interface';
import { troveDetails } from '../../../utils/tokenDetails';
import ActionButton from "../../../theme/components/ActionButton";
import useCore from "../../../hooks/useCore";
import { useWallet } from "use-wallet";
import InfoTip from "../../../theme/components/InfoTip";

interface Iprops {
  trove: string;
  onTroveClick: () => void;
  redeemFee: BasicState;
  stabilityFee: BasicState;
  tvl: {
    isLoading: boolean;
    value: BigNumber;
  };
  usdTvl: BasicState;
}

const MobileRowCard = (props: Iprops) => {
  const { trove, onTroveClick, redeemFee, stabilityFee, tvl, usdTvl } = props;

  const chainId = useGetActiveChainId();
  const core = useCore();
  const { account } = useWallet();

  const troveDetail = troveDetails(trove, chainId);
  const isUserWhiteListed = core.isUserWhiteListed(chainId, account);
  const erc20Token = core.tokens(troveDetail.depositToken, chainId);

  return (
    <CustomGrid>
      <UpperDiv>
        <SymbolDiv>
          <IconLoader
            iconName={troveDetail.depositToken}
            iconType={'tokenSymbol'}
            width={44}
            className="m-r-16"
          />
          <TextWrapper text={troveDetail.displayName} fontWeight={600} />
        </SymbolDiv>
        <div className="single-line-center-between m-b-16">
          <TextWrapper
            text={'REDEEM FEE'}
            fontSize={12}
            fontWeight={600}
            Fcolor={'#FFFFFF88'}
          />
          {redeemFee.isLoading ? (
            <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
          ) : (
            <TextWrapper
              text={
                Numeral(getDisplayBalance(redeemFee.value, 18, 6)).format('0,0.000a') +
                '%'

              }
              fontWeight={600}
              align={'right'}
            />
          )}
        </div>
        <div className="single-line-center-between m-b-16">
          <TextWrapper text={'MAHA FEE'} fontSize={12} fontWeight={600} Fcolor={'#FFFFFF88'} />
          {stabilityFee.isLoading ? (
            <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
          ) : (
            <TextWrapper
              text={
                Numeral(getDisplayBalance(stabilityFee.value, 18, 6)).format('0,0.000a') +
                '%'
              }
              fontWeight={600}
            />
          )}
        </div>
        <div className="single-line-start-between m-b-16">
          <div className="single-line-center-start">
            <TextWrapper text={'TVL'} fontSize={12} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <ToolTip toolTipText={``} />
          </div>
          <div>
            {tvl.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
            ) : (
              <TextWrapper
                text={
                  Numeral(getDisplayBalance(tvl.value, 18, 6)).format('0,0.000a') +
                  ` ${troveDetail.depositToken.toUpperCase()}`
                }
                className={'m-b-4'}
              />
            )}
            {usdTvl.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
            ) : (
              <TextWrapper
                text={`$${Numeral(getDisplayBalance(usdTvl.value, 18, 6)).format('0,0.000a')}`}
                fontWeight={600}
                Fcolor={theme.color.primary[300]}
                align={'right'}
              />
            )}
          </div>
        </div>
        {troveDetail.isDeprecated &&
          <InfoTip type={"Error"} msg={"This collateral is depreciated. Please use some of the other collaterals"} className={"m-b-12"} />
        }
        {troveDetail.isDeprecated
          ? <ActionButton
            text={"Deprecated"}
            onClick={onTroveClick}
            disabled={true}
          />
          : <ActionButton
            text={'Redeem'}
            onClick={onTroveClick}
            tracking_id={'select_redeem'}
            tracking_params={{
              collateral: troveDetail.trove,
            }}
          />
        }
      </UpperDiv>
    </CustomGrid>
  );
};

export default MobileRowCard;

const CustomGrid = styled.div`
  background: linear-gradient(180deg, #48423e 0%, #373030 100%);
  border: 1px solid rgba(255, 255, 255, 0.16);
  box-sizing: border-box;
  border-radius: 12px;
  margin-bottom: 16px;
`;

const UpperDiv = styled.div`
  padding: 32px;
  border-bottom: 1px solid #ffffff29;
  @media (max-width: 600px) {
    padding: 24px;
  }
`;

const SymbolDiv = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
`;
