import config from '../config';

export const troveDetails = (data: string, chainId: number) => {
  return config[chainId].troveStaticInfo.find((item) => item.trove === data);
};

export const liquidityPoolsDetails = (name: string, chainId: number) => {
  return config[chainId].liquidityPoolsStaticInfo.find((item) => item.name === name);
};

export const collateralDetails = (data: string, chainId: number) => {
  return config[chainId].troveStaticInfo.find((item) => item.depositToken === data);
};

export const strategyDetails = (data: string, chainId: number) => {
  return config[chainId].strategiesStaticInfo.find(
    (item) => item.depositTokens.findIndex((p) => p === data) >= 0,
  );
};
