import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import { DECIMALS_18 } from '../../protocol/constants';
import { useGetActiveChainId } from '../../state/chains/hooks';
import useCore from '../useCore';

type State = {
  isLoading: boolean;
  value: Array<any>;
};

export default (doRefresh: boolean) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: [] });

  const core = useCore();
  const activeChainId = useGetActiveChainId();
  const { account } = useWallet();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: [] });
      return;
    }

    const collaterals = core.getAllTroves(activeChainId);

    const finalTroves = [];
    for (let i = 0; i < collaterals.length; i++) {
      const collateral = collaterals[i];

      const troveContract = core.getTroveManager(collateral, activeChainId);
      const trove = await troveContract.Troves(account);
      if (trove.coll.lte(0) && trove.debt.lte(0) && trove.status !== 1) continue;

      const collateralPriceFeedContract = core.getPriceFeed(collateral, activeChainId);
      const collateralPrice = await collateralPriceFeedContract.callStatic.fetchPrice();

      const recoveryMode = await troveContract.checkRecoveryMode(collateralPrice);
      const tcr = await troveContract.getTCR(collateralPrice);

      const spContract = core.getStabilityPool(collateral, activeChainId);
      const arthInSp = await spContract.totalLUSDDeposits();

      const pendingETHReward: any =
        (await troveContract.getPendingETHReward(account)) || BigNumber.from(0);
      const pendingLUSDDebtReward: any =
        (await troveContract.getPendingLUSDDebtReward(account)) || BigNumber.from(0);

      let coll: any = trove.coll;
      let debt: any = trove.debt;
      if (pendingETHReward && pendingETHReward.gt(0)) coll = coll.add(pendingETHReward);
      if (pendingLUSDDebtReward && pendingLUSDDebtReward.gt(0))
        debt = debt.add(pendingLUSDDebtReward);

      const collateralRatio = debt.lte(0)
        ? BigNumber.from(0)
        : coll.mul(collateralPrice).mul(100).div(debt).div(DECIMALS_18);

      const expectedDebt = coll.mul(collateralPrice).mul(100).div(110).div(DECIMALS_18);
      const avail_to_mint = recoveryMode
        ? BigNumber.from(0)
        : debt.lt(expectedDebt)
        ? expectedDebt.sub(debt)
        : BigNumber.from(0);

      const expectedCollateral = BigNumber.from(110)
        .mul(debt)
        .mul(DECIMALS_18)
        .div(collateralPrice)
        .div(100);
      const avail_to_withdraw = recoveryMode
        ? BigNumber.from(0)
        : expectedCollateral.lt(coll)
        ? coll.sub(expectedCollateral)
        : BigNumber.from(0);

      finalTroves.push({
        id: i,
        hasRedistribution: pendingETHReward?.gt(0) || pendingLUSDDebtReward?.gt(0),
        token: collateral,
        avail_to_mint: avail_to_mint,
        deposit: coll,
        debt: debt,
        avail_to_withdraw: avail_to_withdraw,
        collateralRatio: collateralRatio,
        isLiquidateable: recoveryMode
          ? collateralRatio.lt(tcr.mul(100).div(DECIMALS_18)) && arthInSp.gte(debt)
          : collateralRatio.lt(110),
      });
    }

    setValue({ isLoading: false, value: finalTroves });
  }, [account, core, activeChainId]);

  useEffect(() => {
    setValue({ isLoading: true, value: [] });
  }, [account, doRefresh]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch open loans %: ${err}`));
    }
  }, [core.isUnlocked, account, doRefresh, core.myAccount, fetchValue, activeChainId]);

  return value;
};
